import Vue from "vue";
import Vuex from "vuex";
import axios from "axios";
import axiosInstance from "@/main.js"
import { Store, mapState } from "vuex";

axios.defaults.baseURL = "http://localhost:9084";

// Store de users
// import users from "./users";

export default {
  namespaced: true,
  state: {
    campaigns: [],
  },
  mutations: {
    showData(state, payload) {
      state.campaigns = payload;
    },
  },
  actions: {
    getData({ commit }) {
      // Enviar el token como header para la autorización
      /*let config = {
              headers: {
                token: users.state.token,
              },
            };
            Se envía config como argumento a get después de la ruta */
      axios
      .get("/Acampaigns")
        .then((res) => {
          commit("showData", res.data);
        })
        .catch((e) => {
          console.log(e.response.data);
        });
    },
  },
};

