<script>
// Components
import Navbar from "@/components/Navbar.vue";
import ActionsCampaigns from "@/components/ActionsCampaigns.vue";

// Store
import { mapState, mapActions } from "vuex";

export default {
  name: "TestCampaigns",
  components: {
    Navbar,
    ActionsCampaigns
  },
  data() {
    return {
      headers: [
        {
          text: "CAMPAIGN",
          value: "nameCampaign",
          align: "center",
          class: "white navbar4--text body-1 font-weight-bold"
        },
        {
          text: "USER ASSIGNED",
          value: "userAssigned",
          align: "center",
          class: "white activebtn--text body-2"
        },
        {
          text: "START DATE",
          value: "initDate",
          align: "center",
          class: "white activebtn--text body-2"
        },
        {
          text: "END DATE",
          value: "endDate",
          align: "center",
          class: "white activebtn--text body-2"
        },
        {
          text: "CLIENT",
          value: "client",
          align: "center",
          class: "white activebtn--text body-2"
        },
        {
          text: "DEVICES",
          value: "devices",
          align: "center",
          class: "white activebtn--text body-2"
        },
        {
          text: "STATUS",
          value: "status",
          align: "center",
          class: "white activebtn--text body-2"
        },
        {
          text: "TOTAL COST",
          value: "totalCost",
          align: "center",
          class: "white activebtn--text body-2"
        }
      ],
      dataCampaigns: [
        {
          nameCampaign: "Coronavirus",
          userAssigned: "05",
          initDate: "2020-10-05",
          endDate: "2020-11-30",
          client: "Inditex",
          devices: ["Pantalla"],
          status: "Rejected",
          totalCost: "118904"
        },
        {
          nameCampaign: "Comida",
          userAssigned: "03",
          initDate: "2020-10-05",
          endDate: "2020-11-30",
          client: "101 Montaditos",
          devices: ["Led"],
          status: "Approved",
          totalCost: "118904"
        }
      ],
      newDataCampaign: { status: "", endDate: "" },
      dataCampaign: "",
      newStatusCampaign: "",
      campaignInfo: [],
      dialog: false,
      offset: true,
      rightMenu: false,
      date: new Date().toISOString().substr(0, 10),
      menu: false
    };
  },
  computed: {
    ...mapState("campaigns", ["campaigns"])
  },
  methods: {
    ...mapActions("campaigns", ["getData"]),
    getUser(state) {
      if (state == "03")
        return "https://randomuser.me/api/portraits/women/79.jpg";
      else if (state == "05")
        return "https://randomuser.me/api/portraits/men/34.jpg";
    },
    getColor(state) {
      if (state == "Approved") return "#2DCE98";
      else if (state == "Rejected") return "#9A405B";
      else if (state == "") return "#f8f8f8";
    },
    getIcon(state) {
      if (state == "Pantalla") return "$mupi";
      else if (state == "Led") return "$ledscreen";
    },
    addCampaign() {
      this.dataCampaigns.push(this.newDataCampaign);
      this.dialog = false;
      this.newDataCampaign = { status: "" };
    },
    addStatus(item) {
      let data = item;
      data.status = "En espera";
      this.newDataCampaign = data;
    },
    addDate(item) {
      let data = item;
      data.endDate = this.date;
      this.newDataCampaign = data;
      this.newDataCampaign = { endDate: "" };
      this.rightMenu = false;
    },
    async waitData() {
      this.campaignInfo = await this.campaigns;
      return this.campaignInfo;
      console.log(this.campaignInfo);
    }
  },
  created() {
    this.getData();
    this.waitData();
    setTimeout(() => {
      this.campaignInfo = this.campaigns;
      console.log(this.campaignInfo);
    }, 2000);
  }
};
</script>

